<template>
  <div>
    <!-- Text Content -->
    <div
      v-if="element.type === 'text'"
      class="text-content"
      v-html="renderText(element.content)"
    />

    <!-- Image -->
    <OrcaImage
      v-else-if="element.type === 'image'"
      :url="element.content"
      @open-modal="handleImageModal"
    />

    <!-- Video -->
    <OrcaVideo v-else-if="element.type === 'video'" :url="element.content" />

    <!-- YouTube -->
    <OrcaYouTube
      v-else-if="element.type === 'youtube'"
      :url="element.content"
    />

    <!-- Audio -->
    <OrcaAudio
      v-else-if="element.type === 'audio'"
      :audio-items="element.content"
    />

    <!-- Location -->
    <OrcaLocation
      v-else-if="element.type === 'location'"
      :latitude="element.content.latitude"
      :longitude="element.content.longitude"
      :mapbox-token="mapboxToken"
    />

    <!-- Card List -->
    <OrcaCardList
      v-else-if="element.type === 'card'"
      :cards="element.content"
    />

    <!-- Buttons -->
    <OrcaButtons
      v-else-if="element.type === 'buttons'"
      :buttons="element.content"
      :disabled="disabledButton"
      @button-click="handleButtonClick"
    />

    <!-- Tracing -->
    <OrcaTracing
      v-else-if="element.type === 'tracing'"
      :tracing-data="element.content"
      :visibility="visibility"
      :is-admin="isAdmin"
    />

    <!-- HTML Content (matplotlib, plotly, etc.) -->
    <OrcaHtml
      v-else-if="element.type === 'html'"
      :content="element.content"
    />
  </div>
</template>

<script setup lang="ts">
import { useMarkdown } from "../composables/useMarkdown";
import OrcaImage from "./renderers/OrcaImage.vue";
import OrcaVideo from "./renderers/OrcaVideo.vue";
import OrcaYouTube from "./renderers/OrcaYouTube.vue";
import OrcaAudio from "./renderers/OrcaAudio.vue";
import OrcaLocation from "./renderers/OrcaLocation.vue";
import OrcaCardList from "./renderers/OrcaCardList.vue";
import OrcaButtons from "./renderers/OrcaButtons.vue";
import OrcaTracing from "./renderers/OrcaTracing.vue";
import OrcaHtml from "./renderers/OrcaHtml.vue";
import type { ContentPart, ButtonData } from "../types";

/**
 * ContentElement - Router component for different content types
 * Automatically renders the appropriate component based on element type
 */
const props = defineProps<{
  element: ContentPart;
  disabledButton?: boolean;
  visibility?: "all" | "admin";
  isAdmin?: boolean;
  mapboxToken?: string;
}>();

const emit = defineEmits<{
  (e: "image-modal", url: string): void;
  (e: "button-click", button: ButtonData): void;
}>();

const { render: renderText } = useMarkdown();

const handleImageModal = (url: string) => {
  emit("image-modal", url);
};

const handleButtonClick = (button: ButtonData) => {
  emit("button-click", button);
};
</script>
