/**
 * Base Parser Utilities
 * Common logic for all YAML-like parsers
 */

/**
 * Field definition for parsing
 */
export type FieldDef = {
  name: string;
  type?: "string" | "number";
  required?: boolean;
  defaultValue?: any; // Default value if extraction fails
};

/**
 * Create a regex pattern to extract a field value
 * Uses lookahead to match until next field or end
 * Handles multi-line values with indentation and zero-width spaces
 */
function createFieldPattern(
  fieldName: string,
  allFieldNames: string[]
): RegExp {
  // Create lookahead for all other fields
  const otherFields = allFieldNames
    .filter((f) => f !== fieldName)
    .map((f) => `${f}:`)
    .join("|");

  // Pattern that:
  // 1. Matches fieldName:
  // 2. Captures value (including spaces, zero-width spaces, newlines with indentation)
  // 3. Stops at next field (at start of line or after spaces) or end of block

  // Match value until we see another field at any position or end of string
  const pattern = `${fieldName}:\\s*([\\s\\S]*?)(?=\\s*(?:${otherFields})|$)`;
  return new RegExp(pattern, "m");
}

/**
 * Extract field value from block using regex
 * Cleans zero-width spaces and trims whitespace
 */
function extractField(
  block: string,
  fieldName: string,
  allFieldNames: string[]
): string {
  const pattern = createFieldPattern(fieldName, allFieldNames);
  const match = block.match(pattern);

  if (!match || !match[1]) {
    return "";
  }

  let value = match[1];

  // Remove zero-width spaces (\u200b)
  value = value.replace(/\u200b/g, "");

  // For single-line fields, get only the first line
  const firstLine = value.split("\n")[0];

  return firstLine.trim();
}

/**
 * Parse YAML-like list format into array of objects
 *
 * @example
 * ```
 * - field1: value1
 *   field2: value2
 * - field1: value3  field2: value4
 * ```
 */
export function parseYamlList<T>(
  payload: string,
  fields: FieldDef[],
  mapper: (extracted: Record<string, any>) => T
): T[] {
  const fieldNames = fields.map((f) => f.name);

  const blocks = payload
    .split("- ")
    .map((block) => block.trim())
    .filter((block) => block);

  const results = blocks.map((block) => {
    const extracted: Record<string, any> = {};

    // Extract all fields
    for (const field of fields) {
      const value = extractField(block, field.name, fieldNames);

      // Type conversion with fallback to default
      if (field.type === "number") {
        const num = parseInt(value, 10);
        extracted[field.name] = Number.isFinite(num)
          ? num
          : field.defaultValue !== undefined
          ? field.defaultValue
          : undefined;
      } else {
        // Use extracted value, or default value, or empty string for required, or undefined
        extracted[field.name] =
          value ||
          (field.defaultValue !== undefined
            ? field.defaultValue
            : field.required
            ? ""
            : undefined);
      }
    }

    // Map to final type
    return mapper(extracted);
  });

  return results;
}

/**
 * Parse simple key-value format
 *
 * @example
 * ```
 * key1: value1
 * key2: value2
 * ```
 */
export function parseKeyValue(
  payload: string,
  fields: string[]
): Record<string, string> {
  const result: Record<string, string> = {};

  for (const field of fields) {
    const pattern = createFieldPattern(field, fields);
    const match = payload.match(pattern);
    result[field] = match?.[1]?.trim() || "";
  }

  return result;
}
