/**
 * Loading content part types that should be filtered out
 * These are handled separately in the loading indicators section
 */
export const LOADING_CONTENT_TYPES = [
  "general-loading",
  "thinking-loading",
  "searching-loading",
  "coding-loading",
  "analyzing-loading",
  "generating-loading",
  "custom-loading",
  "image-loading",
  "video-loading",
  "youtube-loading",
  "card-loading",
  "map-loading",
] as const;

/**
 * Type guard to check if a content part is a loading type
 */
export function isLoadingType(
  type: string
): type is (typeof LOADING_CONTENT_TYPES)[number] {
  return LOADING_CONTENT_TYPES.includes(type as any);
}
