<template>
  <div v-if="shouldShow" class="orca-trace-container">
    <VCard class="trace-card" elevation="2">
      <!-- Header -->
      <div class="trace-header">
        <div class="trace-header-content">
          <div class="trace-icon">
            <svg
              width="20"
              height="20"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              stroke-width="2"
            >
              <path
                d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z"
              />
            </svg>
          </div>
          <strong class="trace-title">Trace Log</strong>
        </div>
        <VBtn
          size="small"
          variant="tonal"
          color="primary"
          class="trace-toggle-btn"
          @click="toggleTrace"
        >
          {{ isVisible ? "Hide" : "Show" }}
        </VBtn>
      </div>

      <!-- Body -->
      <VExpandTransition>
        <div v-if="isVisible" class="trace-content">
          <pre class="trace-pre">{{ tracingContent }}</pre>
        </div>
      </VExpandTransition>
    </VCard>
  </div>
</template>

<script setup lang="ts">
import { ref, computed } from "vue";
import { VBtn, VCard, VExpandTransition } from "vuetify/components";
import type { TracingData } from "../../types";

/**
 * OrcaTracing - Renders tracing/debug log content
 * Displays trace logs with expand/collapse functionality
 */
const props = defineProps<{
  tracingData: TracingData | TracingData[];
  visibility?: "all" | "admin";
  isAdmin?: boolean;
}>();

const traceVisible = ref(false);

const isVisible = computed(() => traceVisible.value);

const shouldShow = computed(() => {
  if (Array.isArray(props.tracingData)) {
    return props.tracingData.some(
      (item) =>
        item.visibility === "all" ||
        (item.visibility === "admin" && props.isAdmin)
    );
  }

  return (
    props.tracingData.visibility === "all" ||
    (props.tracingData.visibility === "admin" && props.isAdmin)
  );
});

const tracingContent = computed(() => {
  if (Array.isArray(props.tracingData)) {
    return props.tracingData
      .map((item) => item.content || item.rawContent)
      .join("\n\n");
  }

  return props.tracingData.content || props.tracingData.rawContent || "";
});

const toggleTrace = () => {
  traceVisible.value = !traceVisible.value;
};
</script>

