import type { TracingData } from "../../types";

/**
 * Parse tracing data from YAML-like format
 *
 * @template
 * visibility: all
 * content: {"request_id": "req_123", "status": "success"}
 */
export function parseTracing(payload: string): TracingData {
  // Extract visibility (single line)
  const visibilityMatch = payload.match(/visibility:\s*(\w+)/);
  const visibility =
    (visibilityMatch?.[1] as TracingData["visibility"]) || "all";

  // Extract content (everything after "content:")
  const contentMatch = payload.match(/content:\s*([\s\S]*)/);
  const content = contentMatch?.[1]?.trim() || payload;

  return {
    visibility,
    content,
    rawContent: payload,
  };
}
