import type { CardData } from "../../types";
import { parseYamlList, type FieldDef } from "./baseParser";

/**
 * Parse card data from YAML-like format
 *
 * @template
 * - photo: https://example.com/image.jpg
 *   header: Card Title
 *   subheader: Card Subtitle
 */
const CARD_FIELDS: FieldDef[] = [
  {
    name: "photo",
    required: true,
    defaultValue: "https://via.placeholder.com/300x200",
  },
  { name: "header", required: true, defaultValue: "Card Title" },
  { name: "subheader", defaultValue: "" },
];

export function parseCard(payload: string): CardData[] {
  return parseYamlList(payload, CARD_FIELDS, (data) => ({
    photo: data.photo || "",
    header: data.header || "",
    subheader: data.subheader || "",
  }));
}
