import type { AudioData } from "../../types";
import { parseYamlList, type FieldDef } from "./baseParser";

/**
 * Parse audio data from YAML-like format
 *
 * @template
 * - label: Sample Audio 1
 *   url: https://example.com/audio1.mp3
 *   type: audio/mpeg
 */
const AUDIO_FIELDS: FieldDef[] = [
  { name: "label", required: true, defaultValue: "Audio Track" },
  { name: "url", required: true, defaultValue: "" },
  { name: "type", required: true, defaultValue: "audio/mpeg" },
];

export function parseAudio(payload: string): AudioData[] {
  return parseYamlList(payload, AUDIO_FIELDS, (data) => ({
    label: data.label || "",
    url: data.url || "",
    type: data.type || "",
  }));
}
