import type { ContentPart, ContentPartType } from "../../types";
import { removeCompleteMarkers } from "../parsing";
import { GENERAL_LOADING_TYPES } from "../parsing/markerDefinitions";
import { findMatches } from "./matchFinder";
import { processMatch } from "./matchProcessor";

/**
 * Check if type is a general loading type
 * Uses the centralized list from parsing/markerDefinitions
 */
function isGeneralLoadingType(type: ContentPartType): boolean {
  return GENERAL_LOADING_TYPES.includes(type as any);
}

/**
 * Parse content recursively to extract all parts
 * Used when extracting content from inside loading markers
 */
export function parseContentRecursively(content: string): ContentPart[] {
  const parts: ContentPart[] = [];
  let remainingContent = content;
  let iteration = 0;
  const maxIterations = 100;

  while (remainingContent.length > 0 && iteration < maxIterations) {
    iteration++;
    const matches = findMatches(remainingContent);

    if (matches.length === 0) {
      const cleanText = removeCompleteMarkers(remainingContent).trim();
      if (cleanText) {
        parts.push({ type: "text", content: cleanText });
      }
      break;
    }

    const firstMatch = matches.reduce((prev, current) =>
      prev.index < current.index ? prev : current
    );

    const shouldSkip = isGeneralLoadingType(firstMatch.type);

    const textBefore = remainingContent.substring(0, firstMatch.index);
    const cleanTextBefore = removeCompleteMarkers(textBefore).trim();
    if (cleanTextBefore) {
      parts.push({ type: "text", content: cleanTextBefore });
    }

    if (!shouldSkip) {
      const matchPayload = (firstMatch.match[1] ?? "").trim();
      const part = processMatch(firstMatch.type, matchPayload);
      if (part) {
        parts.push(part);
      }
    }

    const matchEnd = firstMatch.index + firstMatch.match[0].length;
    remainingContent = remainingContent.substring(matchEnd);
  }

  return parts;
}
