/**
 * Marker Cleaner - Pure Operations
 * All functions are pure, stateless, and composable
 */

import type { MarkerConfig } from "./markerDefinitions";
import { createMarkerPattern } from "./markerUtils";

/**
 * Remove markers from content using pattern
 * @param content - Content to clean
 * @param pattern - Regex pattern to match
 * @param preserveContent - Keep matched content (group 1) or remove entirely
 */
function removeByPattern(
  content: string,
  pattern: RegExp,
  preserveContent: boolean
): string {
  return content.replace(pattern, preserveContent ? "$1" : "");
}

/**
 * Remove markers from content
 * @param content - Content to clean
 * @param markers - Markers to remove
 * @param preserveContent - Keep content between markers or remove entirely
 */
export function removeMarkers(
  content: string,
  markers: MarkerConfig[],
  preserveContent: boolean = false
): string {
  let cleaned = content;

  for (const marker of markers) {
    const pattern = createMarkerPattern(marker);
    cleaned = removeByPattern(cleaned, pattern, preserveContent);
  }

  return cleaned;
}

/**
 * Remove only loading markers (preserves content between them)
 */
export function removeLoadingMarkers(
  content: string,
  loadingMarkers: MarkerConfig[]
): string {
  return removeMarkers(content, loadingMarkers, true);
}

/**
 * Remove content markers (removes everything)
 */
export function removeContentMarkers(
  content: string,
  contentMarkers: MarkerConfig[]
): string {
  return removeMarkers(content, contentMarkers, false);
}

/**
 * Remove all markers with different strategies
 * @param content - Content to clean
 * @param loadingMarkers - Loading markers (preserve content)
 * @param contentMarkers - Content markers (remove everything)
 */
export function removeAllMarkers(
  content: string,
  loadingMarkers: MarkerConfig[],
  contentMarkers: MarkerConfig[]
): string {
  // First remove loading markers (preserve content)
  let cleaned = removeLoadingMarkers(content, loadingMarkers);

  // Then remove content markers (remove everything)
  cleaned = removeContentMarkers(cleaned, contentMarkers);

  return cleaned;
}
