<template>
  <div class="orca-youtube-container">
    <div class="youtube-wrapper">
      <iframe
        width="100%"
        height="100%"
        :src="embedUrl"
        frameborder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowfullscreen
        class="youtube-iframe"
      />
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed } from "vue";
import { getYouTubeId } from "../../utils/helpers";

/**
 * OrcaYouTube - Renders YouTube video embed
 * Converts YouTube URLs to embed format and displays in iframe
 */
const props = defineProps<{
  url: string;
}>();

const embedUrl = computed(() => {
  const videoId = getYouTubeId(props.url);
  return `https://www.youtube.com/embed/${videoId}`;
});
</script>

<style scoped>
.orca-youtube-container {
  margin: 24px 0;
}

.youtube-wrapper {
  width: 100%;
  max-width: 500px;
  height: 300px;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  background: #000;
}

.youtube-wrapper:hover {
  transform: translateY(-4px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.15),
    0 10px 10px -5px rgba(0, 0, 0, 0.06);
}

.youtube-iframe {
  border-radius: 16px;
  display: block;
}

