import { computed, unref, type ComputedRef, type MaybeRef } from "vue";
import type { ContentPart } from "../types";
import {
  removeCompleteMarkers,
  removeOnlyLoadingMarkers,
  MARKERS,
} from "./parsing";
import { findMatches, processMatch, parseContentRecursively } from "./core";

/**
 * Composable for parsing Orca content markers
 * Supports both reactive and non-reactive values for streaming compatibility
 */
export function useContentParser(
  description: MaybeRef<string>
): ComputedRef<ContentPart[]> {
  return computed<ContentPart[]>(() => {
    const content = unref(description);
    return parseContent(content || "");
  });
}

/**
 * Strip trailing incomplete marker blocks during streaming.
 * If content has [orca.X.start] without a matching [orca.X.end],
 * remove everything from that opening marker onwards so the user
 * doesn't see raw marker text / JSON building up char by char.
 */
function stripIncompleteMarkers(content: string): string {
  let result = content;
  for (const marker of MARKERS) {
    const startIdx = result.lastIndexOf(marker.start);
    if (startIdx === -1) continue;
    // Check if there's a matching end marker after this start
    const afterStart = result.substring(startIdx + marker.start.length);
    if (!afterStart.includes(marker.end)) {
      // Incomplete block: strip from the start marker onwards
      result = result.substring(0, startIdx);
    }
  }
  return result;
}

/**
 * Parse content into parts
 * Extracted for testability
 */
export function parseContent(content: string): ContentPart[] {
  const parts: ContentPart[] = [];
  let remainingContent = stripIncompleteMarkers(content);
  let iteration = 0;
  const MAX_ITERATIONS = 100;

  while (remainingContent.length > 0 && iteration < MAX_ITERATIONS) {
    iteration++;

    const matches = findMatches(remainingContent);

    // No matches: add remaining text and exit
    if (matches.length === 0) {
      addCleanTextIfExists(parts, remainingContent);
      break;
    }

    // Get earliest match
    const firstMatch = matches.reduce((prev, current) =>
      prev.index < current.index ? prev : current
    );

    // Add text before match
    const textBefore = remainingContent.substring(0, firstMatch.index);
    addCleanTextIfExists(parts, textBefore);

    // Process the match
    processMatchAndAddParts(parts, firstMatch);

    // Move to next section
    const matchEnd = firstMatch.index + firstMatch.match[0].length;
    remainingContent = remainingContent.substring(matchEnd);
  }

  return parts;
}

/**
 * Add cleaned text to parts if it exists
 */
function addCleanTextIfExists(parts: ContentPart[], text: string): void {
  const cleanText = removeCompleteMarkers(text).trim();
  if (cleanText) {
    parts.push({ type: "text", content: cleanText });
  }
}

/**
 * Process match and add resulting parts
 */
function processMatchAndAddParts(
  parts: ContentPart[],
  match: { type: any; match: RegExpMatchArray; index: number }
): void {
  const matchPayload = (match.match[1] ?? "").trim();
  const isLoadingType = match.type.includes("-loading");

  // Empty loading marker - skip
  if (isLoadingType && !matchPayload) {
    return;
  }

  // Loading marker with content - parse recursively
  if (isLoadingType && matchPayload) {
    const cleanPayload = removeOnlyLoadingMarkers(matchPayload);
    if (cleanPayload.trim()) {
      const nestedParts = parseContentRecursively(cleanPayload);
      parts.push(...nestedParts);
    }
    return;
  }

  // Regular content type
  const part = processMatch(match.type, matchPayload);
  if (part) {
    parts.push(part);
  }
}
