import type { ContentPartType } from "../../types";
import { CONTENT_PATTERNS } from "../parsing";

export type MatchCandidate = {
  type: ContentPartType;
  match: RegExpMatchArray | null;
  index: number;
};

export type ValidMatch = MatchCandidate & { match: RegExpMatchArray };

/**
 * Find all matches in content
 */
export function findMatches(content: string): ValidMatch[] {
  const matches: MatchCandidate[] = CONTENT_PATTERNS.map(({ type, regex }) => {
    const match = content.match(regex);
    return {
      type,
      match,
      index: match ? content.indexOf(match[0]) : Infinity,
    };
  });

  return matches.filter(
    (m): m is ValidMatch => m.match !== null && m.index !== Infinity
  );
}
