import type { ContentPart, ContentPartType } from "../../types";
import {
  parseCard,
  parseButton,
  parseAudio,
  parseLocation,
  parseTracing,
} from "../parsers";

/**
 * Check if content type is loading type
 * Based on naming convention: all loading types end with "-loading"
 */
function isLoadingType(type: ContentPartType): boolean {
  return type.includes("-loading");
}

/**
 * Content Type Categories
 */
const SIMPLE_CONTENT_TYPES = ["image", "video", "youtube", "html"] as const;

type SimpleContentType = (typeof SIMPLE_CONTENT_TYPES)[number];

/**
 * Parser mapping for complex content types
 */
const PARSER_MAP: Record<string, (payload: string) => any> = {
  card: parseCard,
  location: parseLocation,
  buttons: parseButton,
  tracing: parseTracing,
  audio: parseAudio,
};

/**
 * Process match and return content part
 *
 * @param type - Content type to process
 * @param payload - Content payload
 * @returns ContentPart or null for loading types
 */
export function processMatch(
  type: ContentPartType,
  payload: string
): ContentPart | null {
  // All loading types are handled separately (return null)
  if (isLoadingType(type)) {
    return null;
  }

  // Simple content types (URL strings)
  if (SIMPLE_CONTENT_TYPES.includes(type as SimpleContentType)) {
    return { type, content: payload } as ContentPart;
  }

  // Complex content types (use parser)
  const parser = PARSER_MAP[type];
  if (parser) {
    const content = parser(payload);
    return { type, content };
  }
  return null;
}
