import type { ContentPartType } from "../../types";
import {
  MARKERS,
  GENERAL_LOADING_TYPES,
  type MarkerConfig,
} from "./markerDefinitions";

/**
 * Marker Utilities - Pure Functions
 * No side effects, stateless
 */

/**
 * Escape special regex characters
 */
export function escapeRegex(str: string): string {
  return str.replace(/[.*+?^${}()|[\]\\]/g, "\\$&");
}

/**
 * Create regex pattern from marker config
 */
export function createMarkerPattern(config: MarkerConfig): RegExp {
  const start = escapeRegex(config.start);
  const end = escapeRegex(config.end);
  return new RegExp(`${start}(.*?)${end}`, "s");
}

/**
 * Pattern definition for matching
 */
export type PatternDefinition = {
  type: ContentPartType;
  regex: RegExp;
};

/**
 * Generate patterns from markers
 */
export function generatePatterns(markers: MarkerConfig[]): PatternDefinition[] {
  return markers.map((marker) => ({
    type: marker.type,
    regex: createMarkerPattern(marker),
  }));
}

/**
 * Auto-generated patterns from marker configs
 */
export const CONTENT_PATTERNS: PatternDefinition[] = generatePatterns(MARKERS);

/**
 * Filter markers by predicate
 */
export function filterMarkers(
  markers: MarkerConfig[],
  predicate: (marker: MarkerConfig) => boolean
): MarkerConfig[] {
  return markers.filter(predicate);
}

/**
 * Get marker config by type
 */
export function getMarkerByType(
  markers: MarkerConfig[],
  type: ContentPartType
): MarkerConfig | undefined {
  return markers.find((m) => m.type === type);
}

/**
 * Check if type is loading type
 */
export function isLoadingType(type: ContentPartType): boolean {
  return type.includes("-loading");
}

/**
 * Check if type is general loading type
 * Uses the centralized list from markerDefinitions
 */
export function isGeneralLoadingType(type: ContentPartType): boolean {
  return GENERAL_LOADING_TYPES.includes(type as any);
}

/**
 * Get markers by category
 */
export function getMarkersByCategory(
  markers: MarkerConfig[],
  category: "loading" | "content"
): MarkerConfig[] {
  if (category === "loading") {
    return filterMarkers(markers, (m) => isLoadingType(m.type));
  }
  return filterMarkers(markers, (m) => !isLoadingType(m.type));
}

/**
 * Get general loading markers
 */
export function getGeneralLoadingMarkers(
  markers: MarkerConfig[]
): MarkerConfig[] {
  return filterMarkers(markers, (m) => isGeneralLoadingType(m.type));
}

/**
 * Get content markers (non-loading)
 */
export function getContentMarkers(markers: MarkerConfig[]): MarkerConfig[] {
  return filterMarkers(markers, (m) => !isLoadingType(m.type));
}
