/**
 * Marker Cleaner Facade
 * Provides convenient API using pure functions from markerOperations
 */

import { MARKERS } from "./markerDefinitions";
import { getGeneralLoadingMarkers, getContentMarkers } from "./markerUtils";
import { removeLoadingMarkers, removeAllMarkers } from "./markerOperations";

/**
 * Pre-computed marker sets for performance
 */
const GENERAL_LOADING_MARKERS = getGeneralLoadingMarkers(MARKERS);
const CONTENT_MARKERS = getContentMarkers(MARKERS);

/**
 * Remove only loading markers (preserves content between them)
 * Used when processing complete loading blocks
 */
export function removeOnlyLoadingMarkers(content: string): string {
  return removeLoadingMarkers(content, GENERAL_LOADING_MARKERS);
}

/**
 * Remove complete markers (with both start and end)
 * Preserves incomplete loading markers (only start, no end)
 * Used for cleaning text before matches
 */
export function removeCompleteMarkers(content: string): string {
  return removeAllMarkers(content, GENERAL_LOADING_MARKERS, CONTENT_MARKERS);
}
