<template>
  <div class="orca-html-container">
    <div class="html-wrapper">
      <iframe
        ref="iframeRef"
        :srcdoc="wrappedHtml"
        sandbox="allow-scripts"
        class="html-iframe"
        @load="handleIframeLoad"
      />
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted } from "vue";

/**
 * OrcaHtml - Renders HTML content in a sandboxed iframe
 * Used for interactive content like matplotlib plots, Plotly charts, etc.
 * 
 * Security: Uses sandbox="allow-scripts" which allows JavaScript execution
 * but prevents access to parent page cookies, localStorage, and DOM.
 */
const props = defineProps<{
  content: string;
}>();

const iframeRef = ref<HTMLIFrameElement | null>(null);
const iframeHeight = ref(400);

/**
 * Sanitize HTML content by removing XML declarations and DOCTYPE
 * that would be invalid inside an HTML document body.
 * This is needed for matplotlib SVG output which includes these.
 */
const sanitizeContent = (content: string): string => {
  // Remove XML declaration (e.g., <?xml version="1.0" encoding="utf-8" standalone="no"?>)
  let sanitized = content.replace(/<\?xml[^?]*\?>/gi, '');
  
  // Remove DOCTYPE declarations (e.g., <!DOCTYPE svg PUBLIC ...>)
  sanitized = sanitized.replace(/<!DOCTYPE[^>]*>/gi, '');
  
  return sanitized.trim();
};

/**
 * Wrap the HTML content with proper styling and resize script
 */
const wrappedHtml = computed(() => {
  return `
<!DOCTYPE html>
<html>
<head>
  <meta charset="UTF-8">
  <meta name="viewport" content="width=device-width, initial-scale=1.0">
  <style>
    * {
      box-sizing: border-box;
    }
    html, body {
      margin: 0;
      padding: 0;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, sans-serif;
      background: transparent;
      overflow: hidden;
    }
    body {
      padding: 8px;
    }
    /* SVG responsiveness for matplotlib */
    svg {
      max-width: 100%;
      height: auto;
    }
    /* Plotly container responsiveness */
    .plotly-graph-div {
      width: 100% !important;
    }
  </style>
</head>
<body>
  ${sanitizeContent(props.content)}
  <script>
    // Auto-resize iframe based on content
    function sendHeight() {
      const height = Math.max(
        document.body.scrollHeight,
        document.body.offsetHeight,
        document.documentElement.scrollHeight,
        document.documentElement.offsetHeight
      );
      window.parent.postMessage({ type: 'orca-html-resize', height: height + 16 }, '*');
    }
    
    // Initial send
    setTimeout(sendHeight, 100);
    
    // Send on resize
    window.addEventListener('resize', sendHeight);
    
    // Send when images/content loads
    document.addEventListener('DOMContentLoaded', sendHeight);
    window.addEventListener('load', sendHeight);
    
    // Observe DOM changes for dynamic content (like Plotly)
    const observer = new MutationObserver(sendHeight);
    observer.observe(document.body, { childList: true, subtree: true, attributes: true });
  <\/script>
</body>
</html>`;
});

/**
 * Handle iframe load event
 */
const handleIframeLoad = () => {
  // Initial height adjustment attempt
  adjustHeight();
};

/**
 * Adjust iframe height based on content
 */
const adjustHeight = () => {
  if (iframeRef.value) {
    try {
      // Try to get content height (may fail due to cross-origin)
      const doc = iframeRef.value.contentDocument;
      if (doc && doc.body) {
        const height = Math.max(doc.body.scrollHeight, doc.body.offsetHeight);
        if (height > 0) {
          iframeHeight.value = height + 16;
        }
      }
    } catch (e) {
      // Cross-origin, rely on postMessage
    }
  }
};

/**
 * Listen for resize messages from iframe
 */
const handleMessage = (event: MessageEvent) => {
  if (event.data && event.data.type === 'orca-html-resize') {
    const newHeight = event.data.height;
    if (newHeight && newHeight > 0) {
      iframeHeight.value = Math.min(newHeight, 800); // Max height cap
    }
  }
};

onMounted(() => {
  window.addEventListener('message', handleMessage);
});

onUnmounted(() => {
  window.removeEventListener('message', handleMessage);
});
</script>

<style scoped>
.orca-html-container {
  margin: 16px 0;
}

.html-wrapper {
  width: 100%;
  max-width: 100%;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  background: #ffffff;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.html-wrapper:hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.html-iframe {
  width: 100%;
  height: v-bind(iframeHeight + 'px');
  min-height: 200px;
  max-height: 800px;
  border: none;
  display: block;
  background: transparent;
}

@media (prefers-color-scheme: dark) {
  .html-wrapper {
    background: #1f2937;
  }
}
</style>
