import MarkdownIt from "markdown-it";
import hljs from "highlight.js";
import markdownItLinkAttributes from "markdown-it-link-attributes";
import markdownItKatex from "markdown-it-katex";
import DOMPurify from "dompurify";
import "highlight.js/styles/stackoverflow-light.css";
import "katex/dist/katex.min.css";

// Simple HTML escape function
const escapeCodeHtml = (value: string): string => {
  const map: Record<string, string> = {
    "&": "&amp;",
    "<": "&lt;",
    ">": "&gt;",
    '"': "&quot;",
    "'": "&#039;",
  };
  return value.replace(/[&<>"']/g, (m) => map[m]);
};

// Initialize markdown parser once
const md = new MarkdownIt({
  html: true,
  linkify: true,
  typographer: true,
  highlight: (str, lang) => {
    if (lang && hljs.getLanguage(lang)) {
      try {
        return `<pre><code class="hljs">${
          hljs.highlight(str, { language: lang }).value
        }</code></pre>`;
      } catch {
        // Fallback to escaped HTML
      }
    }
    return `<pre><code class="hljs">${escapeCodeHtml(str)}</code></pre>`;
  },
})
  .use(markdownItKatex)
  .use(markdownItLinkAttributes, {
    pattern: /^https?:\/\//,
    attrs: {
      target: "_blank",
      rel: "noopener noreferrer",
    },
  });

/**
 * Composable for rendering markdown with KaTeX support
 */
export function useMarkdown() {
  /**
   * Render markdown text to HTML
   * @param text - Markdown text to render
   * @returns Rendered HTML
   */
  const render = (text: string): string => {
    const cleanText = text.replace(/\[orca\..*?\]/g, "").trim();
    if (!cleanText) return "";
    const rawHtml = md.render(cleanText);
    return DOMPurify.sanitize(rawHtml, {
      ADD_ATTR: ["style"],
    });
  };

  return { render };
}
