import type { ButtonData } from "../../types";
import { parseYamlList, type FieldDef } from "./baseParser";

/**
 * Parse button data from YAML-like format
 *
 * @template
 * - type: action
 *   label: Get More Info
 *   id: 1
 *   row: 1
 *   color: blue
 */
const BUTTON_FIELDS: FieldDef[] = [
  { name: "type", required: true, defaultValue: "action" },
  { name: "label", required: true, defaultValue: "Button" },
  { name: "url", defaultValue: undefined },
  { name: "id", type: "number", defaultValue: undefined },
  { name: "color", defaultValue: "primary" },
  { name: "row", type: "number", defaultValue: 1 },
];

export function parseButton(payload: string): ButtonData[] {
  const result = parseYamlList(payload, BUTTON_FIELDS, (data) => {
    const type: ButtonData["type"] = data.type === "link" ? "link" : "action";

    return {
      type,
      label: data.label || "",
      url: data.url,
      id: data.id,
      color: data.color,
      row: data.row,
    };
  });

  return result;
}
