<script lang="ts" setup>
import { computed, ref, watch } from "vue";
import "highlight.js/styles/stackoverflow-light.css";
import "katex/dist/katex.min.css";
import { VDialog, VImg, VBtn } from "vuetify/components";
import "../styles/tailwind.css";
import type {
  ButtonData,
  OrcaMarkdownEmits,
  OrcaMarkdownProps,
} from "../types";
import { isImageFile, getFileNameFromUrl } from "../utils/helpers";
import { useContentParser } from "../composables/useContentParser";
import { useLoadingStates } from "../composables/useLoadingStates";
import { useCodeButtons } from "../composables/useCodeButtons";
import { useImageModal } from "../composables/useImageModal";
import { isLoadingType } from "../constants/loadingTypes";
import ContentElement from "./ContentElement.vue";
import GeneralLoading from "./loading/GeneralLoading.vue";
import ImageLoading from "./loading/ImageLoading.vue";
import VideoLoading from "./loading/VideoLoading.vue";
import CardLoading from "./loading/CardLoading.vue";
import MapLoading from "./loading/MapLoading.vue";

const props = withDefaults(defineProps<OrcaMarkdownProps>(), {
  fileAttachments: () => [],
  isLastMessage: false,
  visibility: "all",
});

const emit = defineEmits<OrcaMarkdownEmits>();

// Refs
const messageContentRef = ref<HTMLElement | null>(null);

// Composables
const { modalVisible, currentImage, openModal, closeModal } = useImageModal();
const contentParts = useContentParser(computed(() => props.description));
const {
  isLoading,
  isImageLoading,
  isVideoLoading,
  isCardLoading,
  isLocationLoading,
  getLoadingMessage,
} = useLoadingStates(computed(() => props.description));

// Computed properties
const imageFiles = computed(() => {
  return props.fileAttachments.filter((file) => file && isImageFile(file));
});

const nonImageFiles = computed(() => {
  return props.fileAttachments.filter((file) => file && !isImageFile(file));
});

const disabledButton = computed(() => {
  return props.role !== "assistant" || !props.isLastMessage || isAdminPage();
});

const isAdminPage = () => {
  return props.visibility === "admin";
};

// Get active loading type and message
const getActiveLoadingType = computed(() => {
  const desc = props.description || "";

  // Check each loading type to see which one is active
  if (
    desc.includes("[orca.loading.thinking.start]") &&
    !desc.includes("[orca.loading.thinking.end]")
  ) {
    return "thinking-loading";
  }
  if (
    desc.includes("[orca.loading.searching.start]") &&
    !desc.includes("[orca.loading.searching.end]")
  ) {
    return "searching-loading";
  }
  if (
    desc.includes("[orca.loading.coding.start]") &&
    !desc.includes("[orca.loading.coding.end]")
  ) {
    return "coding-loading";
  }
  if (
    desc.includes("[orca.loading.analyzing.start]") &&
    !desc.includes("[orca.loading.analyzing.end]")
  ) {
    return "analyzing-loading";
  }
  if (
    desc.includes("[orca.loading.generating.start]") &&
    !desc.includes("[orca.loading.generating.end]")
  ) {
    return "generating-loading";
  }
  if (
    desc.includes("[orca.loading.custom.start]") &&
    !desc.includes("[orca.loading.custom.end]")
  ) {
    return "custom-loading";
  }
  if (
    desc.includes("[orca.loading.start]") &&
    !desc.includes("[orca.loading.end]")
  ) {
    return "general-loading";
  }

  return null;
});

// Get current loading message
const getCurrentLoadingMessage = computed(() => {
  if (isLoading.value) {
    const activeType = getActiveLoadingType.value;
    if (activeType) {
      return getLoadingMessage(activeType);
    }
    return "â³ Loading...";
  }
  return "â³ Loading...";
});

// Code buttons composable
const { addCodeButtons } = useCodeButtons(
  messageContentRef,
  () => props.role !== "user"
);

// Watch content parts to update code buttons
watch(contentParts, () => {
  addCodeButtons();
});

/**
 * Handle button click - emit event to parent component
 */
const handleButtonClick = async (button: ButtonData) => {
  if (button.type === "action" && !disabledButton.value) {
    try {
      emit("send-message", {
        message: button.label,
        buttonData: button,
        type: "button-action",
      });
    } catch (error) {
      console.error("Error emitting button click:", error);
    }
  }
};

/**
 * Handle image modal open
 */
const handleImageModal = (url: string) => {
  openModal(url);
};
</script>

<template>
  <div class="message-content">
    <div ref="messageContentRef" class="content-wrapper">
      <!-- Process content parts sequentially -->
      <template v-for="(part, index) in contentParts" :key="index">
        <!-- Skip loading parts - they're handled separately -->
        <ContentElement
          v-if="!isLoadingType(part.type)"
          :element="part"
          :disabled-button="disabledButton"
          :visibility="props.visibility as 'all' | 'admin' | undefined"
          :is-admin="isAdminPage()"
          @image-modal="handleImageModal"
          @button-click="handleButtonClick"
        />
      </template>

      <!-- Content-specific loading placeholders -->
      <GeneralLoading
        :isLoading="isLoading"
        :message="getCurrentLoadingMessage"
      />
      <ImageLoading :isLoading="isImageLoading" />
      <VideoLoading :isLoading="isVideoLoading" />
      <CardLoading :isLoading="isCardLoading" />
      <MapLoading :isLoading="isLocationLoading" />

      <!-- File Attachments Section -->
      <div
        v-if="
          props.fileAttachments &&
          props.fileAttachments.length > 0 &&
          props.role === 'user'
        "
        class="tw-flex tw-flex-wrap tw-gap-4 tw-mt-4 tw-mb-4"
      >
        <!-- Image files -->
        <img
          v-for="file in imageFiles"
          :key="file"
          :src="file"
          class="tw-w-[300px] lg:tw-w-[303px] tw-h-[300px] lg:tw-h-[303px] tw-object-cover tw-rounded-lg tw-cursor-pointer"
          @click="openModal(file)"
        />

        <!-- Non-image files -->
        <VBtn
          v-for="file in nonImageFiles"
          :key="file"
          class="text-center text-sm"
          variant="tonal"
          color="grey-500"
          :href="file"
          target="_blank"
          rel="noopener noreferrer"
        >
          {{ getFileNameFromUrl(file) }}
        </VBtn>
      </div>
    </div>
  </div>

  <VDialog
    v-model="modalVisible"
    fullscreen
    class="image-modal"
    scrim="rgba(0, 0, 0, 0.85)"
    @click="closeModal"
    transition="fade-transition"
  >
    <div class="modal-close-btn" @click.stop="closeModal">
      <button class="close-button" aria-label="Close image">
        <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="2.5"
          stroke-linecap="round"
          stroke-linejoin="round"
        >
          <line x1="18" y1="6" x2="6" y2="18"></line>
          <line x1="6" y1="6" x2="18" y2="18"></line>
        </svg>
      </button>
    </div>
    <div class="modal-content">
      <VImg :src="currentImage" class="modal-image" contain />
    </div>
  </VDialog>
</template>

